.friend_list {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 20px;
    margin-bottom: 100px;
}

.online {
    background-color: rgb(0, 255, 0);
}

.offline {
    background-color: rgb(255, 0, 0);
}

.online,
.offline {
    position: absolute;
    border-radius: 25px;
    width: 20px;
    height: 20px;
}

.avatar {
    margin-top: 25px;
}